import { useState, memo, useEffect } from 'react';
import cls from './Register.module.scss';
import { isMobile } from 'react-device-detect';
import { Button, FieldRow, TaskBoardPanel } from '../../common';
import MessageContainer from '../ProcessesComps/MessageContainer/MessageContainer';
import { createNewCompany } from '../../../services/company.service';
import { take } from 'rxjs';
import { store } from '../../../reduxStore/store';
import { setMessageBar, setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import { useNavigate } from 'react-router-dom';
import MessageBar from '../../TopBar/MessageBar';
import { useSelector } from 'react-redux';
import { checkUserEmail } from '../../../services/user.service';
import { FLOWHERO } from '../../../shared/utility';

const Register = memo(() => {
	const [newCompany, setNewCompany] = useState({ message: '' });
	const { showMessage } = useSelector(store => store.flowReducer);
	const { authenticated } = useSelector(store => store.authReducer);

	const [loading, setLoading] = useState(false);
	const [valid, setValid] = useState(false);

	useEffect(() => {
		store.dispatch(setShowSideMenu(false));
		store.dispatch(setShowMenu(false));
		store.dispatch(setMessageBar({ show: false, message: '' }));
	}, []);

	const navigate = useNavigate();
	const createCompany = () => {
		if (newCompany.companyName && newCompany.displayName && newCompany.email && newCompany.firstName && newCompany.lastName && newCompany.slug) {
			setLoading(true);
			createNewCompany(newCompany)
				.pipe(take(1))
				.subscribe({
					next: () =>
						store.dispatch(
							setMessageBar({ noTimeout: true, show: true, success: true, message: `We have received your request for the creation of ${newCompany.companyName}. We will gladly review this. We will send an email notification to ${newCompany.email}`, action: () => navigate('/login') })
						),
					error: (error) => {
						setLoading(false);
						store.dispatch(setMessageBar({ show: true, message: `Fail! There is something wrong with the details you provided`, color: 'red', error: true, action: () => store.dispatch(setMessageBar({ show: false, message: ``, error: '' })) }));
					},
					complete: () => setLoading(false),
				});
		}
	};

	const checkEmailCompany = () => {
		if (newCompany.email) {
			setLoading(true);
			checkUserEmail(newCompany)
				.pipe(take(1))
				.subscribe({
					next: data => {
						store.dispatch(setMessageBar({ show: true, message: `Email already exists and being used. Please choose another email`, color: 'red', action: () => store.dispatch(setMessageBar({ show: false, message: `` })) }));
						setValid(false);
						setLoading(false);
					},
					error: () => {
						store.dispatch(setMessageBar({ show: true, message: `${newCompany.email} is available for use.`, action: () => store.dispatch(setMessageBar({ show: false, message: `` })) }));
						setLoading(false);
						setValid(true);
					},
				});
		}
	};
	const renderSuperAdminCreateCompany = () => {
		return (
			<div>
				<FieldRow fieldLabel={'Email'} visible={true} style={{ marginTop: '10px' }}>
					<div className={cls.fieldRowContainer}>
						<input
							onChange={e => {
								setNewCompany({ ...newCompany, email: e.target.value });
							}}
							autoComplete="none"
							type='email'
							value={newCompany?.email}
							style={{ border: '1px solid gray' }}
							className={cls.fieldTypeInput}
							name={'Email'}
						/>
					</div>
				</FieldRow>

				<FieldRow fieldLabel={'Company Name'} visible={true} style={{ marginTop: '10px' }}>
					<div className={cls.fieldRowContainer}>
						<input
							onChange={e => {
								setNewCompany({ ...newCompany, companyName: e.target.value, displayName: e.target.value, slug: e.target.value.replaceAll(' ', '').toLowerCase() });
							}}
							minLength={7}
							autoComplete="none"
							style={{ border: '1px solid gray' }}
							className={cls.fieldTypeInput}
							name={'Company Name'}
						/>
					</div>
				</FieldRow>
				<FieldRow fieldLabel={'First Name'} visible={true} style={{ marginTop: '10px' }}>
					<div className={cls.fieldRowContainer}>
						<input
							onChange={e => {
								setNewCompany({ ...newCompany, firstName: e.target.value });
							}}
							autoComplete="none"
							style={{ border: '1px solid gray' }}
							className={cls.fieldTypeInput}
							name={'First Name'}
						/>
					</div>
				</FieldRow>
				<FieldRow fieldLabel={'Last Name'} visible={true} style={{ marginTop: '10px' }}>
					<div className={cls.fieldRowContainer}>
						<input
							onChange={e => {
								setNewCompany({ ...newCompany, lastName: e.target.value });
							}}
							autoComplete="none"
							style={{ border: '1px solid gray' }}
							className={cls.fieldTypeInput}
							name={'Last Name'}
						/>
					</div>
				</FieldRow>
			</div>
		);
	};

	return (
		<div>
			{showMessage?.show && !authenticated ? (
				<MessageBar />
			) : (
				<p
					onClick={() => navigate('/flowhero')}
					style={{
						cursor: 'pointer',
						fontFamily: 'var(--app-text-main-font)',
						color: '#4A4A4A',
						textDecoration: 'underline',
						paddingLeft: '10px',
						//display: isMobile ? 'none' : '',
					}}
				>
					{'Home'}
				</p>
			)}


			<div id="reset-password-container" className={cls.centeredColumn}>
				{(!showMessage?.success && !authenticated) ? <div style={{ marginTop: '100px', padding: '30px', border: '0.5px solid var(--app-color-gray-light)' }}>
					<div
						style={{
							display: 'flex',
							width: '100%',
							flexDirection: 'column',
						}}
					>
						<div
							style={{
								alignSelf: 'flex-start',
								display: 'flex',
								flexDirection: 'column',
								justifyContent: 'space-evenly',
								alignItems: 'center',
							}}
						>
							<div style={{ alignSelf: 'flex-start', width: '350px' }}>
								{FLOWHERO('30px', 'var(--app-color-gray-dark)', 'bold')}
								<p
									style={{
										color: '#4A4A4A',
										fontFamily: 'var(--app-text-main-font)',
										fontSize: '16px',
										fontWeight: 500,
									}}
								>
									{'Sign up your company. Its quick and easy. Input your company email in the field below and follow the instructions in the email sent. Your first 30 days is on us.'}
								</p>
							</div>
							<div style={{ padding: '30px 5px 30px' }}>
								{valid ? (
									renderSuperAdminCreateCompany()
								) : (
									<FieldRow fieldLabel={'Email'} visible={true}>
										<div className={cls.fieldRowContainer}>
											<input
												onChange={e => {
													setNewCompany({ ...newCompany, email: e.target.value });
												}}
												autoComplete="none"
												className={cls.fieldTypeInput}
												style={{ border: '1px solid gray' }}
												name={'Email'}
											/>
										</div>
									</FieldRow>
								)}
								<div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', margin: '0px', width: '100%' }}>
									{valid ? <Button
										clicked={createCompany} title={'Sign me up'} loading={loading} /> :
										<Button
											titleStyle={{ fontWeight: 'normal' }}
											clicked={checkEmailCompany} title={'Check email address availability'} loading={loading} style={{ width: '100%' }} />}
								</div>
							</div>
						</div>
					</div>
				</div> :
					<div style={{ marginTop: '100px', padding: '30px' }}>
						<p
							style={{
								color: '#4A4A4A',
								fontFamily: 'var(--app-text-main-font)',
								fontSize: '18px',
								fontWeight: 'bold',
							}}
						>
							{'We are almost there!'}
						</p>
						{FLOWHERO('30px', 'var(--app-color-gray-dark)', 'bold')}
					</div>
				}
			</div>

		</div>
	);
});

export default Register;
